import React from 'react';

export function Logo({ className = 'h-6 w-auto' }: { className?: string }) {
  return (
    <div className={`flex items-center gap-2 ${className}`}>
      <svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
        <rect x="1" y="1" width="26" height="26" rx="6" stroke="#60A5FA" strokeWidth="2"/>
        <path d="M7 18L12 10L16 16L21 10" stroke="#34D399" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
      </svg>
      <span className="font-semibold">KazSmartChain</span>
    </div>
  );
}


