import './globals.css';
import React from 'react';
import { Providers } from './providers';
import Link from 'next/link';
import { Logo } from '../components/Logo';
import { useUI } from '../store/ui';

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="en" className="dark">
      <body className="min-h-screen">
        <Providers>
          <header className="bg-gray-950 border-b border-gray-800">
            <div className="max-w-7xl mx-auto px-4 py-3 flex items-center justify-between">
              <div className="flex items-center gap-4">
                <Logo />
                <nav className="hidden md:flex gap-4 text-sm text-gray-300">
                  <Link href="/" className="hover:underline">Home</Link>
                  <Link href="/orgs" className="hover:underline">Organizations</Link>
                  <Link href="/network" className="hover:underline">Network</Link>
                  <Link href="/audit" className="hover:underline">Audit</Link>
                </nav>
              </div>
              <TenantSwitcher />
            </div>
          </header>
          <main className="max-w-7xl mx-auto p-4">{children}</main>
        </Providers>
      </body>
    </html>
  );
}

function TenantSwitcher() {
  const selectedTenant = useUI((s) => s.selectedTenant);
  const setTenant = useUI((s) => s.setTenant);
  return (
    <div className="flex items-center gap-2">
      <label className="text-xs text-gray-400">Tenant</label>
      <select
        className="text-sm bg-gray-900 border border-gray-700 rounded px-2 py-1 text-gray-200"
        value={selectedTenant}
        onChange={(e) => setTenant(e.target.value)}
      >
        <option value="demo-bank">Demo Bank</option>
        <option value="demo-uni">Demo University</option>
      </select>
    </div>
  );
}

